import React, { useEffect, useState } from "react";
import figma from "@styles/assets/images/hirescreens/hire-product-designers/figma.svg";
import adobexd from "@styles/assets/images/hirescreens/hire-product-designers/adobe_xd.svg";
import sketch from "@styles/assets/images/hirescreens/hire-product-designers/sketch.svg";
import illustrator from "@styles/assets/images/hirescreens/hire-graphic-designers/adobe_illustrator.svg";
import Photoshop from "@styles/assets/images/hirescreens/hire-graphic-designers/adobe_photoshop.svg";
import notion from "@styles/assets/images/hirescreens/hire-product-designers/notion.svg";
import maze from "@styles/assets/images/hirescreens/hire-product-designers/Maze.svg";
import framer from "@styles/assets/images/hirescreens/hire-product-designers/devicon_framermotion.svg";
import hotjar from "@styles/assets/images/hirescreens/hire-product-designers/hotjar_symbol.svg.svg";
import flowmapp from "@styles/assets/images/hirescreens/hire-product-designers/flowmapp_icon.jpeg.svg";
import balasmic from "@styles/assets/images/hire-uiux-developer/balasmic.svg";
import lucidicon from "@styles/assets/images/hire-uiux-developer/lucidicon.svg";
import zeplin from "@styles/assets/images/hire-uiux-developer/zeplin.svg";

import Ticker from "@components/common/Ticker";

const datas = [
  { id: 0, title: "Figma", icon: figma },
  { id: 1, title: "Adobe XD", icon: adobexd },
  { id: 2, title: "Sketch", icon: sketch },
  { id: 3, title: "Balsamiq", icon: balasmic },
  { id: 4, title: "Notion", icon: notion },
  { id: 5, title: "Zeplin", icon: zeplin },
  { id: 6, title: "Maze", icon: maze },
  { id: 7, title: "Framer", icon: framer },
  { id: 8, title: "Lucidchart", icon: lucidicon },
  { id: 9, title: "Hotjar", icon: hotjar },
  { id: 10, title: "Flowmapp", icon: flowmapp },
  { id: 11, title: "Illustrator", icon: illustrator },
  { id: 12, title: "Photoshop", icon: Photoshop },
];
const TechStackMarquee = () => {
  const [scrollDir, setScrollDir] = useState(null);

  useEffect(() => {
    let scrollSide = window.scrollY;

    const handleScroll = () => {
      let currentScrollSide = window.scrollY;

      if (currentScrollSide > scrollSide) {
        setScrollDir("down");
      } else if (currentScrollSide < scrollSide) {
        setScrollDir("up");
      }
      scrollSide = currentScrollSide;
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <section className="hire-marquee-wrapper">
      <div className="hire-developers-hero-wrapper item-center gap-lg">
        <div className="item-center gap-lg">
          <h2 className="about-h2">Our Technology Stack</h2>
          <p className="about-p c-text-center">
            our UI/UX designers leverage modern design and collaboration tools
            to deliver precision, scalability, and efficiency across every
            project.
          </p>
        </div>
        <section className="token-dev-technology-marquee-section">
          <Ticker
            duration={30}
            direction={`${scrollDir === "up" ? 100 : -100}`}
          >
            {datas.map((item) => (
              <div
                className="token-dev-technology-card item-center"
                key={item.id}
              >
                <div className="flex-center">
                  <img src={item.icon} alt={item.title} />
                  <p className="about-p">{item.title}</p>
                </div>
              </div>
            ))}
          </Ticker>
          <Ticker
            duration={30}
            direction={`${scrollDir === "up" ? -100 : 100}`}
          >
            {datas.map((item) => (
              <div
                className="token-dev-technology-card item-center"
                key={item.id}
              >
                <div className="flex-center">
                  <img src={item.icon} alt={item.title} />
                  <p className="about-p">{item.title}</p>
                </div>
              </div>
            ))}
          </Ticker>
        </section>
      </div>
    </section>
  );
};

export default TechStackMarquee;
